<template>
  <div class="dashboard">
    <!-- 左上角info -->
    <div>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-card shadow="hover" class="mgb20" style="height:252px">
            <div class="user-info">
            <img src="~@/assets/img/img.jpg" class="user-avator" alt=" 头像">
            <div class="user-info-cont">
              <div class="user-info-name">{{name}}</div>
              <div>{{role}}</div>
            </div>
            </div>
            <div class="user-info-list">
              上次登录时间：
              <span>2020-07-30</span>
            </div>
            <div class="user-info-list">
              上次登录地点：
              <span>广州</span>
            </div>
          </el-card>
          <el-card shadow="hover" style="height:252px">
            <div slot="header" class="clearfix">
              <span>语言详情</span>
            </div>
            <span>Vue</span>
            <el-progress :percentage="71.3"  color="#42b983"></el-progress>
            <span>Javascript</span>
            <el-progress :percentage="63.1"  color="#f1e05a"></el-progress>
            <span>Css</span>
            <el-progress :percentage="35.7" ></el-progress>
            <span>Html</span>
            <el-progress :percentage="55.8"  color="#f56c6c"></el-progress>
          </el-card>
        </el-col>
        <el-col :span="16">
          <el-row :gutter="20" class="mgb20">
            <el-col :span="8">
              <el-card shadow="hover" :body-style="{padding: '0px'}">
                <div class="grid-content grid-con-1">
                  <i class="el-icon-user grid-con-icon"></i>
                  <div class="grid-cont-right">
                    <div class="grid-num">1234</div>
                    <div>用户访问</div>
                  </div>
                </div>
              </el-card>
            </el-col>
            <el-col :span="8">
              <el-card shadow="hover" :body-style="{padding: '0px'}">
                <div class="grid-content grid-con-2">
                  <i class="el-icon-bell grid-con-icon"></i>
                  <div class="grid-cont-right">
                    <div class="grid-num">4321</div>
                    <div>系统消息</div>
                  </div>
                </div>
              </el-card>
            </el-col>
            <el-col :span="8">
              <el-card shadow="hover" :body-style="{padding: '0px'}">
                <div class="grid-content grid-con-3">
                  <i class="el-icon-goods grid-con-icon"></i>
                  <div class="grid-cont-right">
                    <div class="grid-num">5000</div>
                    <div>数量</div>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
          <el-card shadow='hover' class="box-action">
            <div slot="header" class="clearfix">
              <span>待办事项</span>
              <el-button @click="addText" style="float: right; padding: 3px 0" type="text">添加</el-button>
              <el-button @click="deleteAllText" style="float: right; padding: 3px 0" type="text">清空</el-button>
            </div>
            <div class="unaction">
              <div class="item" v-for="(item,index) in todoList" :key="item.id">
                <el-checkbox v-model="item.status" :checked="item.status">
                  <div :class="{'text_del': item.status}">{{item.title}}</div>
                </el-checkbox>
                <i class="el-icon-edit edit_icon_style" @click="editItem(index)"></i>
                <i class="el-icon-delete icon_style" @click="deleteItem(index)"></i>
              </div>  
              </div>
          </el-card>
         </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-card shadow='hover'> 
          <schart ref="bar" class="schart" canvasId="bar" :options='options'></schart>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-card shadow='hover'>
            <schart ref="line" class="schart" canvasId='line' :options='options2'></schart>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import schart from 'vue-schart'
export default {
  name: 'dashboard',
  data() {
    return {
      name: localStorage.getItem('ms_username'),
      todolistValue: {},
      todoList: [
        {
          title: '今天要修复100个bug',
          status: false,
          id: 1
        },
        {
          title: '今天要修复100个bug',
          status: false,
          id: 2
        },
        {
          title: '今天要修复100个bug',
          status: false,
          id: 3
        },
        {
          title: '今天要修复100个bug',
          status: false,
          id: 4
        },
        {
          title: '今天要修复100个bug',
          status: true,
          id: 5
        },
        {
          title: '今天要修复100个bug',
          status: true,
          id: 6
        }
      ],
      id: 6,
      options: {
                  type: 'bar',
                  title: {
                      text: '最近一周各品类销售图'
                  },
                  xRorate: 25,
                  labels: ['周一', '周二', '周三', '周四', '周五'],
                  datasets: [
                      {
                          label: '家电',
                          data: [234, 278, 270, 190, 230]
                      },
                      {
                          label: '百货',
                          data: [164, 178, 190, 135, 160]
                      },
                      {
                          label: '食品',
                          data: [144, 198, 150, 235, 120]
                      }
                  ]
              },
     options2: {
       type: 'line',
       title: {
         text: '最近几个月各类销售趋势图'
       },
       labels: ['6月','7月','8月','9月','10月'],
       datasets: [
         {
           label: '家电',
           data: [234,278,270,190,230]
         },
         {
           label: '百货',
           data: [164,178,150,190,130]
         },
         {
           label: '食品',
           data: [74,120,116,178,80]
         }
       ]
     }
    }
  },
  computed: {
    role () {
      return this.name === 'admin' ? '超级管理员':'普通用户';
    }
  },
  components: {
    schart
  },
  methods: {
    addText () {
       this.$prompt('请输入内容', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
          inputErrorMessage: '内容过长或为空'
        }).then(({ value }) => {
          if (this.todoList.length >= 8){
             this.$message({
              message: '最多只能添加8条哦！',
              type: 'warning'
            });
            return false
          }else {
           this.$message({
            type: 'success',
            message: '新事项: ' + value
          });
          this.todolistValue.title = value;
          this.$set(this.todolistValue,'status',false)
          var id = ++this.id
          this.todolistValue.id = id
          this.todoList.push(this.todolistValue)
          console.log(this.todolistValue);
          this.todolistValue = {}
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
    },
    // 删除当前item
    deleteItem (index) {
       this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.todoList.splice(index,1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });

    },
    editItem (index) {
       this.$prompt('请输入内容', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: this.todoList[index].title,
          inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
          inputErrorMessage: '内容过长或为空'
        }).then(({ value }) => {
          /* this.todolistValue.id = this.todoList[index].id
          this.todolistValue.title =  value
          this.$set(this.todolistValue,'status',false)
          this.todoList.splice(index,1,this.todolistValue)
          this.todolistValue = {} */
          this.$set(this.todoList[index],'title',value)
          this.$forceUpdate();
           this.$message({
            type: 'success',
            message: '新修改事项内容为: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
      });
    },
    deleteAllText() {
       this.$confirm('是否清空全部内容?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.todoList = []
          this.id = 0
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    }
  }
}
</script>

<style scoped>
  .dashboard {
    min-width: 1200px;
  }
  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }
  .grid-content {
    display: flex;
    align-items: center;
    height: 100px;
    border-radius: 4px;
  }
  .grid-cont-right {
    flex: 1;
    text-align: center;
    font-size: 14px;
    color: #999;
  }
  .grid-num {
    font-size: 30px;
    font-weight: bold;
  }
  .grid-con-icon {
    font-size: 50px;
    width: 100px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    color: #fff;
  }

  .user-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 2px #ccc solid;
    padding: 10px 0;
}
  img.user-avator {
    width: 120px;
    height: 120px;
    border-radius: 50%;
  }
  .user-info-cont {
    padding-left: 50px;
    flex: 1;
    font-size: 14px;
    color: #999;
  }
  .user-info-name {
    font-size: 30px;
    color: #222;
  }
  .user-info-list {
    margin-top: 12px;
    font-size: 14px;
    color: #999;
  }

  .mgb20 {
    margin-bottom: 20px;
  }
  .grid-con-1 .grid-con-icon {
    background: rgb(45,140,240);
  }
  .grid-con-1 .grid-num {
    color: rgb(45,140,240);
  }
  .grid-con-2 .grid-con-icon {
    background: rgb(100,213,114);
  }
  .grid-con-2 .grid-num {
    color: rgb(45, 140, 240);
  }
  .grid-con-3 .grid-con-icon {
    background: rgb(242,94,67);
  }
  .grid-con-3 .grid-num {
    color: rgb(242,94,67)
  }
  .unaction .item {
    padding: 10px 0;
    border-bottom: 1px #eee solid;
  }
  .box-action {
    min-height: 402px;
    max-height: 402px;
  }
  .text_del {
    color: #999 !important;
    text-decoration: line-through;
}
.schart {
  width: 100%;
  height: 300px;
}
.unaction .item {
  position: relative;
}

.unaction .item .icon_style{
  position: absolute;
  top: 17px;
  right: 10px;
  cursor: pointer;
}
.unaction .item .edit_icon_style {
   position: absolute;
  top: 17px;
  right: 30px;
  cursor: pointer;
}
.unaction .item .edit_icon_style:hover, .unaction .item .icon_style:hover {
  color: red !important;
}
</style>